	module.exports = {"pathContext":{"group":[{"node":{"id":"51479f0f-5e67-5b18-beda-1208ed4aa8ce","slug":"open-access-week-the-next-generation","title":"Open Access Week: the next generation","excerpt":"<p>Open Access Week, a global event now entering its eighth year and running from the 20th – 26th October, is an opportunity for the academic and research community to continue to learn about the potential benefits of Open Access, to share what they’ve learned with colleagues, and to help inspire wider participation in helping to [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/10/20/open-access-week-the-next-generation/\">Read More&#8230;</a></p>\n","status":"publish","date":"October 20, 2014","template":"","format":"standard"}},{"node":{"id":"fcbd788a-cdd4-5bd3-b044-656506cf6eb5","slug":"undergraduate-bullyingskirt-size-and-tobacco-packaging-most-read-articles-in-september","title":"Undergraduate bullying,skirt size and tobacco packaging: Most Read Articles in September","excerpt":"<p>This month&#8217;s top ten most accessed articles includes a systematic review by Abu Dabrh et al. on the health assessment of commercial drivers, Marston et al&#8217;s  study on anal sex and young people is still proving to be a popular read,  Timm&#8217;s survey reporting on medical undergraduate students and bullying is a new entry, whilst Lusignan et al discuss patient&#8217;s online access [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/10/06/undergraduate-bullyingskirt-size-and-tobacco-packaging-most-read-articles-in-september/\">Read More&#8230;</a></p>\n","status":"publish","date":"October 06, 2014","template":"","format":"standard"}},{"node":{"id":"72648353-8564-57b5-87a7-a876709a840c","slug":"moderate-weekly-alcohol-intake-linked-to-poorer-sperm-quality-in-healthy-young-men","title":"Moderate weekly alcohol intake linked to poorer sperm quality in healthy young men","excerpt":"<p>Moderate alcohol intake of at least five units every week is linked to poorer sperm quality in otherwise healthy young men, suggests research published in the online journal BMJ Open. And the higher the weekly tally of units, the worse the sperm quality seems to be, the findings indicate, prompting the researchers to suggest that [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/10/02/moderate-weekly-alcohol-intake-linked-to-poorer-sperm-quality-in-healthy-young-men/\">Read More&#8230;</a></p>\n","status":"publish","date":"October 02, 2014","template":"","format":"standard"}},{"node":{"id":"14005e01-4f52-5516-8121-4cd86271ff6f","slug":"salty-cheese-unprepared-graduates-and-coersive-anal-sex-most-read-articles-in-august","title":"Salty cheese, unprepared graduates and coercive anal sex: most read articles in August","excerpt":"<p>August&#8217;s top ten includes the highly accessed, survey based study by Marston et al. on anal sex amongst young people,  a study on Ecuadorian medical graduates being prepared enough for a year of  compulsory rural service in obstetrics and a popular cross-sectional survey by Hashem et al. of salt content in cheese which concludes that it is [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/09/29/salty-cheese-unprepared-graduates-and-coersive-anal-sex-most-read-articles-in-august/\">Read More&#8230;</a></p>\n","status":"publish","date":"September 29, 2014","template":"","format":"standard"}},{"node":{"id":"767a0893-40d7-5590-bb53-8ced2312732a","slug":"skirt-size-increase-linked-to-33-greater-postmenopausal-breast-cancer-risk","title":"Skirt size increase linked to 33% greater postmenopausal breast cancer risk","excerpt":"<p>Going up a skirt size over a period of 10 years between your mid 20s and mid 50s is linked to a 33% greater risk of developing breast cancer after the menopause, finds a large observational study published in BMJ Open. Overall weight gain during adulthood is known to be a risk factor for breast cancer, [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/09/24/skirt-size-increase-linked-to-33-greater-postmenopausal-breast-cancer-risk/\">Read More&#8230;</a></p>\n","status":"publish","date":"September 24, 2014","template":"","format":"standard"}},{"node":{"id":"6145671d-1720-5d02-b530-fb4eb284d570","slug":"gun-deaths-twice-as-high-among-african-americans-as-white-citizens-in-us","title":"Gun deaths twice as high among African Americans as white citizens in US","excerpt":"<p>Gun deaths are twice as high among African Americans as they are among white citizens in the US, finds a study of national data, published in the online journal BMJ Open. But the national figures, which have remained relatively steady over the past decade, mask wide variation in firearms deaths by ethnicity and state, the [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/09/18/gun-deaths-twice-as-high-among-african-americans-as-white-citizens-in-us/\">Read More&#8230;</a></p>\n","status":"publish","date":"September 18, 2014","template":"","format":"standard"}},{"node":{"id":"e886fb6a-b796-5eeb-b33e-2cb7cb6dac5f","slug":"plain-cigarette-packs-dont-hurt-small-retailers-or-boost-trade-in-illicit-tobacco","title":"Plain cigarette packs don’t hurt small retailers or boost trade in illicit tobacco","excerpt":"<p>Plain packs for tobacco products don’t hurt small retailers, flood the market with very cheap cigarettes, or boost the trade in illicit tobacco, finds research on the early experience of the policy in Australia, and published in journal BMJ Open. The findings suggest there is no evidence for these particular arguments against the policy, put forward [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/08/28/plain-cigarette-packs-dont-hurt-small-retailers-or-boost-trade-in-illicit-tobacco/\">Read More&#8230;</a></p>\n","status":"publish","date":"August 28, 2014","template":"","format":"standard"}},{"node":{"id":"1d2e4b93-1a04-5d71-bc16-f6d1656fa136","slug":"bmj-open-now-publishes-cohort-profiles","title":"BMJ Open now publishes cohort profiles","excerpt":"<p>&nbsp; BMJ Open currently publishes articles reporting research results or study protocols. We have now expanded our scope to include cohort profiles, articles that describe major, ongoing research cohorts. What’s the difference between a protocol, a cohort profile and a research paper? Detailed information about cohort profiles is in our instructions for authors. In brief, cohort [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/08/22/bmj-open-now-publishes-cohort-profiles/\">Read More&#8230;</a></p>\n","status":"publish","date":"August 22, 2014","template":"","format":"standard"}},{"node":{"id":"3dc87bdd-25fb-50a7-920a-bf9d2860d999","slug":"anal-sex-between-young-men-and-women-often-seems-coercive-and-painful","title":"Anal sex between young men and women often seems coercive and painful","excerpt":"<p>Anal sex between young men and women often seems coercive and painful, suggests research published in BMJ Open. Feedback from young people reveals an oppressive culture around anal sex, with some young men apparently neglecting or not caring about young women’s consent or pleasure &#8211; both when they have anal sex and when they talk [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/08/13/anal-sex-between-young-men-and-women-often-seems-coercive-and-painful/\">Read More&#8230;</a></p>\n","status":"publish","date":"August 13, 2014","template":"","format":"standard"}},{"node":{"id":"9fad035e-8ff8-5aff-b240-d876e680b5b5","slug":"cheese-still-laden-with-salt-despite-many-products-meeting-reduction-targets","title":"Cheese still laden with salt, despite many products meeting reduction targets","excerpt":"<p>The salt content of cheese sold in UK supermarkets remains high, despite many products meeting the recommended government targets on salt reduction, finds research published in the online journal BMJ Open. And salt content varies widely, even within the same type of cheese, the findings show, prompting the researchers to call for much tougher targets [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2014/08/06/cheese-still-laden-with-salt-despite-many-products-meeting-reduction-targets/\">Read More&#8230;</a></p>\n","status":"publish","date":"August 06, 2014","template":"","format":"standard"}}],"pathPrefix":"","first":false,"last":false,"index":15,"pageCount":26,"additionalContext":{}}}

